ations */\n.board-section {\n  animation: fadeIn 0.5s ease-out;\n}\n\n.game-board {\n  animation: slideIn 0.3s ease-out;\n}\n\n.board-cell.hit {\n  animation: hitPulse 0.6s ease-out;\n}\n\n.board-cell.hit::after {\n  animation: hitRotate 0.6s ease-out;\n}\n\n.board-cell.sunk {\n  animation: sunkPulse 1s ease-in-out infinite alternate;\n}\n\n.board-cell.preview-valid {\n  animation: previewPulse 1s ease-in-out infinite alternate;\n}\n\n.board-cell.preview-invalid {\n  animation: invalidShake 0.5s ease-in-out;\n}\n\n@keyframes modalSlideIn {\n  from {\n    opacity: 0;\n    transform: translateY(-50px) scale(0.9);\n  }\n  to {\n    opacity: 1;\n    transform: translateY(0) scale(1);\n  }\n}\n\n/* Responsive Design */\n@media (max-width: 768px) {\n  .container {\n    padding: 10px;\n  }\n\n  .game-header {\n    flex-direction: column;\n    gap: 15px;\n    text-align: center;\n    padding: 15px;\n  }\n\n  .game-header h1 {\n    font-size: 2rem;\n  }\n\n  .game-boards {\n    flex-direction: column;\n    gap: 20px;\n  }\n\n  .game-board {\n    grid-template-columns: repeat(10, 32px);\n    grid-template-rows: repeat(10, 32px);\n    padding: 10px;\n  }\n\n  .board-cell {\n    width: 32px;\n    height: 32px;\n    font-size: 14px;\n  }\n\n  .board-cell.hit::after,\n  .board-cell.sunk::after {\n    font-size: 16px;\n  }\n\n  .board-cell.miss::after {\n    font-size: 14px;\n  }\n\n  .modal-content {\n    padding: 30px 20px;\n    margin: 20px;\n  }\n\n  .placement-controls {\n    flex-direction: column;\n    align-items: center;\n  }\n\n  .btn {\n    padding: 10px 20px;\n    font-size: 13px;\n  }\n}\n\n@media (max-width: 480px) {\n  .game-board {\n    grid-template-columns: repeat(10, 28px);\n    grid-template-rows: repeat(10, 28px);\n  }\n\n  .board-cell {\n    width: 28px;\n    height: 28px;\n    font-size: 12px;\n  }\n\n  .game-header h1 {\n    font-size: 1.5rem;\n  }\n}\n\n/* Loading Animation */\n.loading {\n  display: inline-block;\n  width: 20px;\n  height: 20px;\n  border